<template>
  <div class="sdlogin">
    <!--  -->
    <div class="sdbox">
      <!-- logo -->
      <div class="sdimg">
        <img src="logo.png" alt />
      </div>
      <!--  -->
      <div class="sdinput">
        <div class="sdpad">
          <el-input placeholder="" v-model="username">
            <i slot="prefix" class="el-icon-user-solid"></i>
          </el-input>
        </div>
        <div class="sdpad">
          <el-input placeholder="" v-model="password" show-password>
            <i slot="prefix" class="el-icon-lock"></i>
          </el-input>
        </div>
        <!--  -->
        <div class="sdann">
          <el-row>
            <el-button type="primary" @click="login()"></el-button>
            <el-button type="info" ></el-button>
          </el-row>
        </div>
      </div>
      <!-- ------------- -->
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  // 
  name: "demo",
  //  
  props: [],
  // 
  components: {},
  // 
  data() {
    return {
      username: "admin",
      password: "123456"
    };
  },
  // 
  computed: {},
  // 
  watch: {},
  // 
  methods: {
    login() {
      axios
        .post("http://127.0.0.1:8888/api/private/v1/login", {
          username: this.username,
          password: this.password
        })
        .then(res => {
          console.log(res)
          if (res.data.meta.msg == "") {
               this.$message({
        message: "",
        type: "success"
      });
            sessionStorage.setItem("token", res.data.data.token);
            // that.globalData.token = res.data.data.token;
            this.$router.push("/Home/Welcome");
          } else {
      this.$message.error("");

          }
        });
    },
   
    
  },
  //  
  /**
   * data
   */
  beforeCreate() {},
  /**
   * DOM$ el
   */
  created() {},
  /**
   *  render 
   */
  beforeMount() {},
  /**
   * el  vm.$ el 
   *  root  mounted  vm.$ el 
   */
  mounted() {},
  /**
   *  DOM 
   * 
   */
  beforeUpdate() {},
  /**
   *  DOM 
   *  DOM  DOM 
   */
  updated() {},
  /**
   * keep-alive  keep-alive 
   */
  activated() {},
  /**
   * keep-alive  keep-alive 
   */
  deactivated() {},
  /**
   * 
   */
  beforeDestroy() {},
  /**
   * Vue Vue 
   * 
   */
  destroyed() {}
};
</script> 

