<template>
  <div>
    <!-- <h1></h1> -->
    <!--  -->
    <div>
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/Home/Welcome' }"></el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/Home/roles' }"></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <!-- =============================================================== -->
    <!-- {{tableData}} -->
    <!--  -->
    <div class="sdtable">
      <!--  -->
      <el-row>
        <el-button type="primary"></el-button>
        <!-- ************************************************************************ -->
         </el-row>
      <div class="sdtable">
        <!--  -->
        <el-table :data="tableData" border style="width: 100%" stripe>
          <el-table-column type="index" label width="50"></el-table-column>

          <el-table-column type="index" label="#" width="50"></el-table-column>
          <el-table-column prop="roleName" label="" width="180"></el-table-column>
          <el-table-column prop="roleDesc" label=""></el-table-column>
          <el-table-column prop="address" label="">
            <template slot-scope="scope">
              <el-tooltip
                :enterable="false"
                content=""
                placement="top"
                @click="dialogFormVisible = true"
              >
                <el-button
                  size="mini"
                  @click="showdigth(scope.row.id)"
                  type="primary"
                  icon="el-icon-edit"
                  circle
                ></el-button>
              </el-tooltip>

              <el-tooltip :enterable="false" content="" placement="top">
                <el-button
                  @click="open(scope.row.id)"
                  size="mini"
                  type="danger"
                  icon="el-icon-delete"
                  circle
                ></el-button>
              </el-tooltip>

              <el-tooltip :enterable="false" content="" placement="top">
                <el-button size="mini" type="warning" icon="el-icon-setting" circle></el-button>
              </el-tooltip>
            </template>
          </el-table-column>
        </el-table>
        <!-- ================================================================ -->




















        
        <!-- 

        <el-dialog title="" :visible.sync="dialogFormVisible" width="50%">
          -----------------------------------
          
          <el-form
            :model="editForm"
            :rules="rulesFn"
            ref="editForm"
            label-width="100px"
            class="demo-ruleForm"
          >
            <el-form-item label="" prop="username">
              <el-input v-model="editForm.username"></el-input>
            </el-form-item>
          </el-form>
          -----------------------------------------------------

          
          <el-form
            :model="editForm"
            :rules="rulesFn"
            ref="editForm"
            label-width="100px"
            class="demo-ruleForm"
          >
            <el-form-item label="" prop="usersname">
              <el-input v-model="editForm.usersname"></el-input>
            </el-form-item>
          </el-form>
           ---------------------------------------------------------------

          <span slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false"> </el-button>
            <el-button type="primary" @click="editUsers"> </el-button>
          </span>
        </el-dialog> -->
      </div>
    </div>
  </div>
</template>

<script>
import { ajaxRolesxg } from "../utils/api";

export default {
  // 
  name: "demo",
  //  
  props: [],
  // 
  components: {},
  // 
  data() {
    return {
      tableData: [],
      dialogVisible: false,
      dialogTableVisible: false,
      dialogFormVisible: false,
      editForm: {
        username: "",
        usersname: "",
      },
      rulesFn: {
        username: [
          { required: true, message: "", trigger: "blur" },
          { min: 6, max: 20, message: " 6  20 ", trigger: "blur" }
        ],
        usersname: [
          { required: true, message: "", trigger: "blur" },
          { min: 6, max: 20, message: " 6  20 ", trigger: "blur" }
        ]
      }
    };
  },
  // 
  computed: {},
  // 
  watch: {},
  // 
  methods: {
    //   tijiao
    editUsers() {
      this.$refs.editForm.validate(async valid => {
        // console.log(valid)/\
        if (!valid) return;
        const { data: res } = await this.$http.put(
          "users/" + this.editForm.id,
          {
            email: this.editForm.username,
            mobile: this.editForm.username
          }
        );
        if (res.meta.status !== 200) {
          return this.$message.error("xxx");
        }

        this.dialogFormVisible = false;
        this.userFn();
        this.$message.success("");
      });
    },
    async showdigth(data) {
      let { data: res } = await ajaxRolesxg(this.editForm);
      console.log(res);
      if (res.meta.status == 200) {
        // alert(111)
        this.editForm = res.data;
        this.$message({
          message: res.meta.msg,
          duration: 1000,
          type: "success"
        });
      } else {
        return this.$message.error("");
      }
      this.dialogVisible = true;
    },
    open() {
      this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          //   sessionStorage.removeItem("tableData.id");
          //    this.tableData.splice(index,1)
          this.$message({
            type: "success",
            message: "!"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    }
  },
  //  
  /**
   * data
   */
  beforeCreate() {},
  /**
   * DOM$ el
   */
  created() {},
  /**
   *  render 
   */
  beforeMount() {},
  /**
   * el  vm.$ el 
   *  root  mounted  vm.$ el 
   */
  mounted() {
    this.$axios.get("https://www.liulongbin.top:8888/api/private/v1/roles").then(res => {
      // console.log(res);
      this.tableData = res.data.data;
    });
  },
  /**
   *  DOM 
   * 
   */
  beforeUpdate() {},
  /**
   *  DOM 
   *  DOM  DOM 
   */
  updated() {},
  /**
   * keep-alive  keep-alive 
   */
  activated() {},
  /**
   * keep-alive  keep-alive 
   */
  deactivated() {},
  /**
   * 
   */
  beforeDestroy() {},
  /**
   * Vue Vue 
   * 
   */
  destroyed() {}
};
</script> 

