<template>
  <div>
    <h1></h1>
    <!--  -->
    <div>
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/Home/Welcome' }"></el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/Home/users' }"></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <!-- -------------------------------------------- -->
    <!-- users    table -->
    <div class="sdtable">
      <!--   -->
      <div style="margin-top: 15px;" class="sdidf">
        <el-input
          @clear="userFn"
          clearable
          placeholder=""
          v-model="goodsParams.query"
          class="input-with-select"
          size="60%"
        >
          <el-button @click="userFn" slot="append" icon="el-icon-search"></el-button>
        </el-input>
        <!--  -->
        <el-row>
          <el-button type="primary" @click="dialogFormVisible = true"></el-button>
        </el-row>
      </div>
      <!-- ----------------------------------------------------- -->
      <!-- ------------------------------ -->
      <!--     -->
      <div class="sdbody">
        <el-table :data="tableData" stripe style="width: 100%">
          <el-table-column type="index" label="#" width="50"></el-table-column>
          <el-table-column prop="goods_name" label="" width="700"></el-table-column>
          <el-table-column prop="goods_price" label="()" width="150"></el-table-column>
          <el-table-column prop="goods_weight" label="" width="150"></el-table-column>
          <el-table-column prop="add_time" label="" width="150"></el-table-column>
          <el-table-column prop="address" label="" width="150">
            <template slot-scope="scope">
              <el-tooltip
                :enterable="false"
                content=""
                placement="top"
                @click="dialogVisible = true"
              >
                <el-button
                  size="mini"
                  @click="showdigth(scope.row.id)"
                  type="primary"
                  icon="el-icon-edit"
                  circle
                ></el-button>
              </el-tooltip>

              <el-tooltip :enterable="false" content="" placement="top">
                <el-button
                  @click="open(scope.row.id)"
                  size="mini"
                  type="danger"
                  icon="el-icon-delete"
                  circle
                ></el-button>
              </el-tooltip>
            </template>
          </el-table-column>
        </el-table>

        <!-- ----------------------------- -->
        <!--  -->
        <div class="block">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="goodsParams.pagenum"
            :page-sizes="[1, 2, 5, 10]"
            :page-size="goodsParams.pagesize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="totalNum"
          ></el-pagination>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ajaxGoodsList } from "../utils/api";
export default {
  // 
  name: "demo",
  //  
  props: [],
  // 
  components: {},
  // 
  data() {
    return {
      goodsParams: {
        query: "",
        pagenum: 1,
        pagesize: 10
      },
      dialogVisible: false,
      value: false,
      totalNum: 0,
      tableData: [],

      dialogTableVisible: false,
      dialogFormVisible: false
    };
  },
  // 
  computed: {},
  // 
  watch: {},
  // 
  methods: {
    async userFn() {
      let { data: res } = await ajaxGoodsList(this.goodsParams);
      //   console.log(res);
      this.tableData = res.data.goods;
      this.totalNum = res.data.total;
    },
    handleSizeChange(size) {
      this.goodsParams.pagesize = size;
      this.userFn();
    },
    handleCurrentChange(page) {
      this.goodsParams.pagenum = page;
      this.userFn();
    }
  },
  //  
  /**
   * data
   */
  beforeCreate() {},
  /**
   * DOM$ el
   */
  created() {},
  /**
   *  render 
   */
  beforeMount() {},
  /**
   * el  vm.$ el 
   *  root  mounted  vm.$ el 
   */
  mounted() {
    this.userFn();
  },
  /**
   *  DOM 
   * 
   */
  beforeUpdate() {},
  /**
   *  DOM 
   *  DOM  DOM 
   */
  updated() {},
  /**
   * keep-alive  keep-alive 
   */
  activated() {},
  /**
   * keep-alive  keep-alive 
   */
  deactivated() {},
  /**
   * 
   */
  beforeDestroy() {},
  /**
   * Vue Vue 
   * 
   */
  destroyed() {}
};
</script> 

<!-- Add "scoped" attribute to limit CSS to this component only -->
<!--scopedstyle-->
<!--scopedcssscopedcss-->
<!---->
<style scoped>
.sdidf {
  display: flex;
  margin: 20px;
}
.el-button {
  margin-left: 10px;
}
.el-input-group {
  line-height: normal;
  display: inline-table;
  width: 60%;
  border-collapse: separate;
  border-spacing: 0;
}
.sdtable {
  width: 100%;
  height: 100%;
  margin: 10px;
  padding: 10px;
  background: #fff;
}
</style>
