<template>
  <div>
    <!-- <h1></h1> -->
    <!--  -->
    <div>
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/Home/Welcome' }"></el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/Home/users' }"></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <!-- -------------------------------------------- -->
    <!-- users    table -->
    <div class="sdtable">
      <!--   -->
      <div style="margin-top: 15px;" class="sdidf">
        <el-input
          @clear="userFn"
          clearable
          placeholder=""
          v-model="userParams.query"
          class="input-with-select"
          size="60%"
        >
          <el-button @click="userFn" slot="append" icon="el-icon-search"></el-button>
        </el-input>
        <!--  -->
        <el-row>
          <el-button type="primary" @click="dialogFormVisible = true"></el-button>
        </el-row>

        <el-dialog title="" :visible.sync="dialogFormVisible">
          <!--  -->
          <el-form
            :model="ruleForm"
            :rules="rules"
            ref="ruleForm"
            label-width="100px"
            class="demo-ruleForm"
          >
            <el-form-item label="" prop="username">
              <el-input v-model="ruleForm.username"></el-input>
            </el-form-item>
          </el-form>
          <!-- ----------------------------------------------------- -->
          <!--  -->
          <el-form
            :model="ruleForm"
            :rules="rules"
            ref="ruleForm"
            label-width="100px"
            class="demo-ruleForm"
          >
            <el-form-item label="" prop="password">
              <el-input v-model="ruleForm.password"></el-input>
            </el-form-item>
          </el-form>
          <!-- ----------------------------------------------------- -->
          <!--  -->
          <el-form
            :model="ruleForm"
            :rules="rules"
            ref="ruleForm"
            label-width="100px"
            class="demo-ruleForm"
          >
            <el-form-item label="" prop="email">
              <el-input v-model="ruleForm.email"></el-input>
            </el-form-item>
          </el-form>
          <!-- ----------------------------------------------------- -->
          <!--  -->
          <el-form
            :model="ruleForm"
            :rules="rules"
            ref="ruleForm"
            label-width="100px"
            class="demo-ruleForm"
          >
            <el-form-item label="" prop="mobile">
              <el-input v-model="ruleForm.mobile"></el-input>
            </el-form-item>
          </el-form>
          <!-- --------------------------------------------------------------- -->
          <!--  -->
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false"> </el-button>
            <el-button type="primary" @click="submitForm('ruleForm')"> </el-button>
          </div>
        </el-dialog>
        <!-- ----------------------------------------------------- -->
      </div>
      <!-- ------------------------------ -->
      <!--     -->
      <div class="sdbody">
        <el-table :data="tableData" stripe style="width: 100%">
          <el-table-column type="index" label="#" width="50"></el-table-column>
          <el-table-column prop="username" label="" width="110"></el-table-column>
          <el-table-column prop="email" label=""></el-table-column>
          <el-table-column prop="create_time" label=""></el-table-column>
          <el-table-column prop="role_name" label=""></el-table-column>
          <el-table-column prop="mg_state" label="">
            <template slot-scope="scope">
              <el-switch
                @change="changeState(scope.row.id,scope.row.mg_state)"
                v-model="scope.row.mg_state"
                active-color="#409EFF"
                inactive-color="#DCDFE6"
              ></el-switch>
            </template>
          </el-table-column>
          <el-table-column prop="address" label="">
            <template slot-scope="scope">
              <el-tooltip
                :enterable="false"
                content=""
                placement="top"
                @click="dialogVisible = true"
              >
                <el-button
                  size="mini"
                  @click="showdigth(scope.row.id)"
                  type="primary"
                  icon="el-icon-edit"
                  circle
                ></el-button>
              </el-tooltip>

              <el-tooltip :enterable="false" content="" placement="top">
                <el-button
                  @click="open(scope.row.id)"
                  size="mini"
                  type="danger"
                  icon="el-icon-delete"
                  circle
                ></el-button>
              </el-tooltip>
              <!-- <template>
                    <el-button type="text" @click="open"> Message Box</el-button>
              </template>-->
              <el-tooltip :enterable="false" content="" placement="top">
                <el-button size="mini" type="warning" icon="el-icon-setting" circle></el-button>
              </el-tooltip>
            </template>
          </el-table-column>
        </el-table>
        <!--  -->

        <el-dialog title="" :visible.sync="dialogVisible" width="50%">
          <!-- ----------------------------------- -->
          <!--  -->
          <el-form
            disabled
            :model="editForm"
            :rules="rulesFn"
            ref="editForm"
            label-width="100px"
            class="demo-ruleForm"
          >
            <el-form-item label="" prop="username">
              <el-input v-model="editForm.username"></el-input>
            </el-form-item>
          </el-form>
          <!-- ----------------------------------------------------- -->

          <!--  -->
          <el-form
            :model="editForm"
            :rules="rulesFn"
            ref="editForm"
            label-width="100px"
            class="demo-ruleForm"
          >
            <el-form-item label="" prop="email">
              <el-input v-model="editForm.email"></el-input>
            </el-form-item>
          </el-form>
          <!-- ----------------------------------------------------- -->
          <!--  -->
          <el-form
            :model="editForm"
            :rules="rulesFn"
            ref="editForm"
            label-width="100px"
            class="demo-ruleForm"
          >
            <el-form-item label="" prop="mobile">
              <el-input v-model="editForm.mobile"></el-input>
            </el-form-item>
          </el-form>
          <!--  --------------------------------------------------------------- -->

          <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false"> </el-button>
            <el-button type="primary" @click="editUsers"> </el-button>
          </span>
        </el-dialog>

        <!-- ----------------------------- -->
        <!--  -->
        <div class="block">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="userParams.pagenum"
            :page-sizes="[1, 2, 5, 10]"
            :page-size="userParams.pagesize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="totalNum"
          ></el-pagination>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {
  ajaxUserList,
  ajaxChangeState,
  ajaxAddUsers,
  ajaxxg,
  ajaxsc
} from "../utils/api";
export default {
  // 
  name: "demo",
  //  
  props: [],
  // 
  components: {},
  // 
  data() {
    //   
    let checkEmail = (rule, value, callback) => {
      let em = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/;
      if (!value) {
        return callback(new Error("Email"));
      }
      if (em.test(value)) {
        callback();
      } else {
        callback(new Error("Email"));
      }
    };
    // 
    let checkMobile = (rule, value, callback) => {
      let phone = /^1[3|4|5|8][0-9]\d{4,8}$/;
      if (!value) {
        return callback(new Error(""));
      }
      if (phone.test(value)) {
        callback();
      } else {
        callback(new Error(""));
      }
    };
    return {
      dialogVisible: false,
      value: false,
      totalNum: 0,
      tableData: [],

      userParams: {
        query: "",
        pagenum: 1,
        pagesize: 5
      },
      rules: {
        username: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 18, message: " 3  18 ", trigger: "blur" }
        ],
        password: [
          { required: true, message: "", trigger: "blur" },
          { min: 6, max: 20, message: " 6  20 ", trigger: "blur" }
        ],
        email: [
          { required: true, message: "", trigger: "blur" },
          { validator: checkEmail, trigger: "blur" }
        ],
        mobile: [
          { required: true, message: "", trigger: "blur" },
          { validator: checkMobile, trigger: "blur" }
        ]
      },
      ruleForm: {
        username: "",
        password: "",
        email: "",
        mobile: ""
      },
      editForm: {
        password: "",
        email: "",
        mobile: ""
      },
      rulesFn: {
        password: [
          { required: true, message: "", trigger: "blur" },
          { min: 6, max: 20, message: " 6  20 ", trigger: "blur" }
        ],
        email: [
          { required: true, message: "", trigger: "blur" },
          { validator: checkEmail, trigger: "blur" }
        ],
        mobile: [
          { required: true, message: "", trigger: "blur" },
          { validator: checkMobile, trigger: "blur" }
        ]
      },
      dialogTableVisible: false,
      dialogFormVisible: false
    };
  },
  // 
  computed: {},
  // 
  watch: {},
  // 
  methods: {
    //   
    // async open(id){
    //     const confirmResult=await this.$confirm('', '', {
    //       confirmButtonText: '',
    //       cancelButtonText: '',
    //       type: 'warning'
    //     }).catch(err=>err)
    //     if(confirmResult !=='confirm'){
    //         return this.$message.info('')
    //     }
    //     const {data:res}=await ajaxsc(id)
    //      if (res.meta.status == 200) {

    //     this.$message({
    //       message: res.meta.msg,
    //       duration: 1000,
    //       type: "success"
    //     });
    //   }
    // },
    open() {
      this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          //   sessionStorage.removeItem("tableData.id");
          //    this.tableData.splice(index,1)
          this.$message({
            type: "success",
            message: "!"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    //   tijiao
    editUsers() {
      this.$refs.editForm.validate(async valid => {
        // console.log(valid)/\
        if (!valid) return;
        const { data: res } = await this.$http.put(
          "users/" + this.editForm.id,
          {
            email: this.editForm.email,
            mobile: this.editForm.mobile
          }
        );
        if (res.meta.status !== 200) {
          return this.$message.error("xxx");
        }

        this.dialogVisible = false;
        this.userFn();
        this.$message.success("");
      });
    },

    async showdigth(id) {
      let { data: res } = await ajaxxg(id);
      console.log(res);
      if (res.meta.status == 200) {
        // alert(111)
        this.editForm = res.data;
        this.$message({
          message: res.meta.msg,
          duration: 1000,
          type: "success"
        });
      } else {
        return this.$message.error("");
      }
      this.dialogVisible = true;
    },
    handleClose(done) {
      this.$confirm("")
        .then(_ => {
          done();
        })
        .catch(_ => {});
    },
    // list
    async userFn() {
      let { data: res } = await ajaxUserList(this.userParams);
        console.log(res);
      this.tableData = res.data.users;
      this.totalNum = res.data.total;
    },
    handleSizeChange(size) {
      this.userParams.pagesize = size;
      this.userFn();
    },
    handleCurrentChange(page) {
      this.userParams.pagenum = page;
      this.userFn();
    },
    async submitUserAdd() {
      let { data: res } = await ajaxAddUsers(this.ruleForm);
      if (res.meta.status == 201) {
        this.$message({
          message: res.meta.msg,
          duration: 1000,
          type: "success"
        });
        this.dialogFormVisible = false;
        this.userFn();
      }
    },
    // 
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.submitUserAdd();
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    // 
    async changeState(uId, bool) {
      // console.log(bool)
      let { data: res } = await ajaxChangeState(uId, bool);
      // console.log(res)
      if (res.meta.status == 200) {
        this.$message({
          message: res.meta.msg,
          duration: 1000,
          type: "success"
        });
      }
    }
  },
  //  
  /**
   * data
   */
  beforeCreate() {},
  /**
   * DOM$ el
   */
  created() {},
  /**
   *  render 
   */
  beforeMount() {},
  /**
   * el  vm.$ el 
   *  root  mounted  vm.$ el 
   */
  mounted() {
    this.userFn();
  },
  /**
   *  DOM 
   * 
   */
  beforeUpdate() {},
  /**
   *  DOM 
   *  DOM  DOM 
   */
  updated() {},
  /**
   * keep-alive  keep-alive 
   */
  activated() {},
  /**
   * keep-alive  keep-alive 
   */
  deactivated() {},
  /**
   * 
   */
  beforeDestroy() {},
  /**
   * Vue Vue 
   * 
   */
  destroyed() {}
};
</script> 

<!-- Add "scoped" attribute to limit CSS to this component only -->
<!--scopedstyle-->
<!--scopedcssscopedcss-->
<!---->
<style scoped>
.el-button {
  margin-left: 10px;
}
.sdbody {
  margin: 10px;
}
.sdidf {
  display: flex;
  margin: 20px;
}
.el-input-group {
  line-height: normal;
  display: inline-table;
  width: 60%;
  border-collapse: separate;
  border-spacing: 0;
}
.sdtable {
  width: 100%;
  height: 100%;
  margin: 10px;
  padding: 10px;
  background: #fff;
}
</style>
