<template>
  <div>
    <!--  -->
    <div>
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/Home/Welcome' }"></el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/Home/users' }"></el-breadcrumb-item>
      </el-breadcrumb>
    </div>

    <div class="sdbody">
        <el-table :data="tableData" stripe style="width: 100%">
          <el-table-column type="index" label="#" width="50"></el-table-column>
          <el-table-column prop="order_number" label="" width="190"></el-table-column>
          <el-table-column prop="order_price" label="" width="150"></el-table-column>
          <el-table-column prop="order_pay" label="" width="150"></el-table-column>
          <el-table-column prop="is_send" label="" width="150"></el-table-column>
          <el-table-column prop="create_time" label="" width="150"></el-table-column>
          <el-table-column prop="address" label="" width="150">
            <template slot-scope="scope">
              <el-tooltip
                :enterable="false"
                content=""
                placement="top"
                @click="dialogVisible = true"
              >
                <el-button
                  size="mini"
                  @click="showdigth(scope.row.id)"
                  type="primary"
                  icon="el-icon-edit"
                  circle
                ></el-button>
              </el-tooltip>

              <el-tooltip :enterable="false" content="" placement="top">
                <el-button
                  @click="open(scope.row.id)"
                  size="mini"
                  type="danger"
                  icon="el-icon-delete"
                  circle
                ></el-button>
              </el-tooltip>
            </template>
          </el-table-column>
        </el-table>

        <!-- ----------------------------- -->
        <!--  -->
        <div class="block">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="ordersParams.pagenum"
            :page-sizes="[1, 2, 5, 10]"
            :page-size="ordersParams.pagesize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="totalNum"
          ></el-pagination>
        </div>
      </div>
  </div>
</template>

<script>
import { ajaxOrdersList } from "../utils/api";

export default {
  // 
  name: "demo",
  //  
  props: [],
  // 
  components: {},
  // 
  data() {
    return {
      ordersParams: {
        query: "",
        pagenum: 1,
        pagesize: 10
      },
      dialogVisible: false,
      value: false,
      totalNum: 0,
      tableData: [],

      dialogTableVisible: false,
      dialogFormVisible: false
    };
  },
  // 
  computed: {},
  // 
  watch: {},
  // 
  methods: {
    // list
    async userFn() {
      let { data: res } = await ajaxOrdersList(this.ordersParams);
        console.log(res);
      this.tableData = res.data.goods;
      this.totalNum = res.data.total;
    },
    handleSizeChange(size) {
      this.ordersParams.pagesize = size;
      this.userFn();
    },
    handleCurrentChange(page) {
      this.ordersParams.pagenum = page;
      this.userFn();
    }
  },
  //  
  /**
   * data
   */
  beforeCreate() {},
  /**
   * DOM$ el
   */
  created() {},
  /**
   *  render 
   */
  beforeMount() {},
  /**
   * el  vm.$ el 
   *  root  mounted  vm.$ el 
   */
  mounted() {
    this.userFn();
  },
  /**
   *  DOM 
   * 
   */
  beforeUpdate() {},
  /**
   *  DOM 
   *  DOM  DOM 
   */
  updated() {},
  /**
   * keep-alive  keep-alive 
   */
  activated() {},
  /**
   * keep-alive  keep-alive 
   */
  deactivated() {},
  /**
   * 
   */
  beforeDestroy() {},
  /**
   * Vue Vue 
   * 
   */
  destroyed() {}
};
</script> 

